
  import {defineComponent} from 'vue';

  export default defineComponent({
    name: 'BaseModal',
    props: {
      title: {
        type: String,
        default: 'title',
      },
      isShown: {
        type: Boolean,
        default: true,
      },
      width: {
        type: Number,
        default: 618,
      },
    },
    emits: ['cancel', 'submit', 'update:isShown'],
    setup(props, {emit}) {
      const onCancel = () => {
        emit('cancel');
        emit('update:isShown', false);
      };
      const onSubmit = () => {
        emit('submit');
      };
      return {
        onCancel,
        onSubmit,
      };
    },
  });
